<template>
  <div class="address" id="address_page">
    <backeheader title="收获地址" />
    <scroll ref="scroll" clickable>
      <div v-for="item in list" :key="item.id" @click="goBack(item)" class="address-item">
        <div class="icon-wrap">
          <van-icon v-if="item.id === selected" name="success" class="selected-icon" />
        </div>
        <div class="info">
          <div class="user">
            <p class="name">{{ item.name }}</p>
            <p class="phone">{{ item.mobile }}</p>
<!--            <p v-if="item.tag" :class="['tag', tagClass[item.tag]]">{{ item.tag }}</p>-->
            <p class="tag" v-if="item.default">默认</p>
          </div>
          <p class="desc">{{ item.province + item.city + item.district }}{{ item.address }}</p>
        </div>
        <img @click.stop="goEdit(item)" src="./bianji@3x.png" alt="" class="edit">
      </div>
    </scroll>
    <button class="create" @click="$router.push('/address/edit')">+新建地址</button>
  </div>
</template>

<script>
import Backeheader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'
import { setItem } from '../../common/js/storage'

export default {
  name: 'addresses',
  components: {
    Backeheader,
    Scroll
  },
  data() {
    return {
      list: [],
      tagClass: {
        家: 'red',
        公司: 'blue',
        学校: 'orange'
      },
      selected: 0
    }
  },
  mounted() {
    this.getList()
    this.$nextTick(() => {
      this.$refs.scroll.refresh()
      this.selected = Number(this.$route.params.id)
    })
  },
  methods: {
    goBack(address) {
      this.selected = address.id
      setItem('selectAddress', address)
      this.$router.goBack()
    },
    goEdit(item) {
      this.$router.push({
        name: 'address-edit',
        params: {
          address: { ...item }
        }
      })
    },
    getList() {
      const loading = this.$skeLoading('#address_page')
      post('/shop/user/getUserAddressList')
        .then(({ data: { data } }) => {
          this.list = data
          loading.close()
        })
        .catch(() => {
          loading.close()
        })
    }
  }
}
</script>

